<template>
    <section class="pages container">
        <review-list :isUserShowen="true" :reviews="reviews"></review-list>
    </section>
</template>

<script>
import reviewList from "@/components/review-list";
import ReviewList from "../components/review-list.vue";

export default {
    computed: {
        reviews() {
            return this.$store.getters.reviews;
        },
    },
    created() {
        this.$store.dispatch({
            type: "loadReviews",
        });
    },
    components: {
        reviewList,
    },
};
</script>
