<template>
    <section v-if="toy" class="toy-details flex space-around pages container">
        <div class="details-container">
            <div class="img-container ratio-square img-detail">
                <img class="toy-img" :src="imgSrc" alt="" />
            </div>
            <h2>{{ toy.name }}</h2>
            <h3>Price: {{ toy.price }}</h3>
            <h3>Type: {{ toy.type }}</h3>
            <h3 class="inStock" v-if="toy.inStock">In stock</h3>
            <h3 class="outOfStock" v-else>Out of stock</h3>
            <div class="comment" v-if="!isAddReviewShowen">
                Thanks for your review!
            </div>
            <div
                class="add-review flex column"
                v-if="loggedInUser && isAddReviewShowen"
            >
                <form @submit.prevent="addReview">
                <h3>Add review</h3>
                    <textarea
                        name="content"
                        cols="20"
                        rows="4"
                        v-model="review.content"
                    ></textarea>
                    <star-rating
                        @click="setRating"
                        class="star-rating"
                        :increment="0.5"
                        :max-rating="5"
                        inactive-color="white"
                        active-color="gold"
                        :star-size="20"
                        :border-width="1"
                        v-model="review.rating"
                        border-color="#999"
                    >
                    </star-rating>
                    <button type="submit">Add</button>
                </form>
            </div>
        </div>
        <div class="reviews-container">
            <h2>Reviews</h2>
            <review-list
                v-if="reviews.length"
                :isUserShowen="true"
                :reviews="reviews"
            ></review-list>
            <el-button type="button" @click="goBack">Back</el-button>
        </div>
    </section>
</template>

<script>
import { toyService } from "../services/toy.service.js";
import StarRating from "vue-star-rating";
import reviewList from "@/components/review-list";

export default {
    data() {
        return {
            review: null,
            toy: null,
            loggedInUser: null,
            isAddReviewShowen: true,
        };
    },
    computed: {
        imgSrc() {
            return this.toy.imgUrl
                ? this.toy.imgUrl
                : "https://www.carrefourbahrain.com/sites/default/files/7.jpg";
        },
        reviews() {
            return this.$store.getters.reviews;
        },
        dateForDisplay() {
            return moment(this.review.createdAt).format("MMM Do YY");
        },
    },
    methods: {
        goBack() {
            this.$router.push("/");
        },
        setRating(rating) {
            this.review.rating = rating;
        },

        addReview() {
            this.$store.dispatch({
                type: "addReview",
                review: this.review,
            });
            // this.reviews.unshift(this.review);
            this.isAddReviewShowen = false;
        },
        setReview() {
            this.review = {
                aboutToy: { toyId: this.toy._id, name: this.toy.name },
                content: "",
                rating: null,
                createdAt: Date.now(),
            };
        },
    },
    async created() {
        const toyId = this.$route.params.id;
        const toy = await toyService.getById(toyId);
        this.toy = toy;
        this.loggedInUser = this.$store.getters.loggedinUser;
        if (this.loggedInUser) this.setReview();
        this.$store.dispatch({
            type: "loadReviews",
            filterBy: { toyId: this.toy._id },
        });
    },
    components: {
        StarRating,
        reviewList,
    },
};
</script>

